import Vue from "vue";
import { getUserInfo, login, logout } from "@/api/user";
import {
  getAccessToken,
  removeAccessToken,
  setAccessToken,
} from "@/utils/accessToken";
import { resetRouter } from "@/router";
import { title, tokenName } from "@/config/settings";

const state = {
  accessToken: getAccessToken(),
  username: "",
  avatar: "",
  permissions: [],
};
const getters = {
  accessToken: (state) => state.accessToken,
  username: (state) => state.username,
  avatar: (state) => state.avatar,
  permissions: (state) => state.permissions,
};
const mutations = {
  setAccessToken(state, accessToken) {
    state.accessToken = accessToken;
    setAccessToken(accessToken);
  },
  setUsername(state, username) {
    state.username = username;
  },
  setAvatar(state, avatar) {
    state.avatar = avatar;
  },
  setPermissions(state, permissions) {
    state.permissions = permissions;
  },
};
const actions = {
  setPermissions({ commit }, permissions) {
    commit("setPermissions", permissions);
  },
  async login({ commit }, userInfo) {
    const { accessToken } = await login(userInfo); //
    // const accessToken = data[tokenName]; //token
    console.log(accessToken + "===============");
    if (accessToken) {
      commit("setAccessToken", accessToken);
      const hour = new Date().getHours();
      const thisTime =
          hour < 8
              ? ""
              : hour <= 11
              ? ""
              : hour <= 13
                  ? ""
                  : hour < 18
                      ? ""
                      : "";
      Vue.prototype.$baseNotify(``, `${thisTime}`);
    } else {
      Vue.prototype.$baseMessage(
          `...`,
          "error"
      );
     throw ""
    }
  },
  async getUserInfo({ commit, state }) {
    const { permissions, username, avatar } = await getUserInfo(state.accessToken);
    // if (!data) {
    //   Vue.prototype.$baseMessage("...", "error");
    //   return false;
    // }
    // let { permissions, username, avatar } = data;
    console.log( permissions)
    console.log( username)
    console.log( avatar)
    if (permissions && username && Array.isArray(permissions)) {
      commit("setPermissions", permissions);
      commit("setUsername", username);
      commit("setAvatar", avatar);
      return permissions;
    } else {
      Vue.prototype.$baseMessage("", "error");
      return false;
    }
  },
  async logout({ dispatch }) {
    await logout(state.accessToken);
    await dispatch("resetAccessToken");
    await resetRouter();
  },
  resetAccessToken({ commit }) {
    commit("setUsername", "");
    commit("setPermissions", []);
    commit("setAccessToken", "");
    removeAccessToken();
  },
};
export default { state, getters, mutations, actions };
