import router from "@/router";
import store from "@/store";
import VabProgress from "nprogress";
import "nprogress/nprogress.css";
import getPageTitle from "@/utils/pageTitle";
import {
  authentication,
  loginInterception,
  progressBar,
  recordRoute,
  routesWhiteList,
} from "./settings";
import Vue from "vue";

VabProgress.configure({
  easing: "ease",
  speed: 500,
  trickleSpeed: 200,
  showSpinner: false,
});

//
router.beforeResolve(async (to, from, next) => {
  console.log(
    from.path + "-------------------------------" + to.path
  );

  if (progressBar) VabProgress.start(); //true  
  let hasToken = store.getters["user/accessToken"]; //tokentoken  null
  if (!loginInterception) hasToken = true; //   loginInterception 

  if (hasToken) {
    //token
    console.log("");
    if (to.path === "/login") {
      //login
      next({ path: "/" }); //
      if (progressBar) VabProgress.done(); //
    } else {
      const hasPermissions = // 
        store.getters["user/permissions"] &&
        store.getters["user/permissions"].length > 0;
      if (hasPermissions) {
        //
        console.log("");
        next();
      } else {
        //
        console.log("");
        try {
          let permissions; //
          if (!loginInterception) {
            //settings.js loginInterceptionfalse
            await store.dispatch("user/setPermissions", ["customer"]);
            // "admin","customer","boss"
            permissions = "customer";
          } else {
            //  
            permissions = await store.dispatch("user/getUserInfo");
          }

          let accessRoutes = []; //

            accessRoutes = await store.dispatch(
              "routes/setRoutes",
              permissions
            );

          console.log("permissions+++++" + permissions);
          router.addRoutes(accessRoutes);
          console.log(router)
          // if (
          //   permissions.indexOf("manager") !== -1 ||
          //   permissions.indexOf("boss") !== -1
          // ) {
          //   next({ path: "/manager" });
          // } else {
            next({ ...to, replace: true });
          // }
        } catch {
          await store.dispatch("user/resetAccessToken");
          if (progressBar) VabProgress.done();
        }
      }
    }
  } else {
    //

    if (routesWhiteList.indexOf(to.path) !== -1) {
      //token 
      /* if (to.path === "/login") {
        console.log("66666666666666666666");
        to.query.type = from.query.type;
        console.log(from.query.type);
        console.log(from.params.type);
      }*/
      next();
    } else {
      console.log("to.path++++++++++++++++" + to.path);

      if (recordRoute) {
        //token
        if (to.path === "/manager") {
          next(`/login?redirect=${to.path}&&type=manager`);
        } else {
          next(`/login?redirect=${to.path}&&type=customer`);
        }
      } else {
        next("/login");
      }

      if (progressBar) VabProgress.done();
    }
  }
  document.title = getPageTitle(to.meta.title);
});//





router.afterEach(() => {
  if (progressBar) VabProgress.done();
});
